export function createMenu(httpClient: HttpClient): CreateMenuSignature {
  return (menu: Menu) =>
    universalCreateMenu(
      menu,
      // @ts-ignore
      { httpClient }
    );
}

interface CreateMenuSignature {
  /**
   * > **Note:** The Menus API only works with the Wix Restaurants Menus (New) app. Make sure you have installed this app from the [Wix App Market](https://www.wix.com/app-market/wix-restaurants-menus-new).
   *
   * Creates a menu.
   *
   * To create multiple menus at once, use [Bulk Create Menus](https://dev.wix.com/docs/rest/business-solutions/restaurants/menus/menu/bulk-create-menus).
   * @param - Menu details.
   * @returns Menu.
   */
  (menu: Menu): Promise<
    NonNullablePaths<
      Menu,
      | `name`
      | `sectionIds`
      | `seoData.tags`
      | `seoData.tags.${number}.type`
      | `seoData.tags.${number}.children`
      | `seoData.tags.${number}.custom`
      | `seoData.tags.${number}.disabled`
      | `seoData.settings.preventAutoRedirect`
      | `seoData.settings.keywords`
      | `seoData.settings.keywords.${number}.term`
      | `seoData.settings.keywords.${number}.isMain`,
      6
    >
  >;
}

